.root {
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
}

.main {
  flex: 1;
}

:global(.mdc-toolbar) button:global(.material-icons) {
  height: auto !important;
  width: auto !important;
  min-width: 24px !important;
  padding: 0 8px 0 0 !important;
  color: white !important;
}




